/* ==========================================================================
   Footer responsive style - begin
   ========================================================================== */

@include ipad-landscape {
    
    .eltdf-page-footer {
        
        .eltdf-column-content {
	        
	        @media only screen and (min-width: $ipad-portrait) {
		
		        &.eltdf-grid-col-3 {
			        width: 50%;
					float: left;
			        
			        &:nth-child(2n+1) {
				        clear: both;
			        }
		        }
		
		        &.eltdf-grid-col-4 {
			        width: 33.33333333333333%;
			        float: left;
		        }
	        }
        }
		.eltdf-footer-bottom-holder {
			@include eltdfRelativeHolderLayout();
			background-color: #212121;
			box-sizing: border-box;
			text-align: center;
			&.eltdf-light {
				background-color: #fff;
			}

			.eltdf-footer-bottom-inner {
				&.eltdf-full-width,
				&.eltdf-grid{
					.eltdf-grid-row {
						> div {
							text-align: center!important;
							&:last-child {
								margin-top: 20px;
							}
						}
					}
				}
			}
		}
    }
}

@include phone-landscape {

    .eltdf-page-footer {

        .eltdf-column-content {
            
            &:not(:first-child) {
                margin-top: 20px;
            }
        }
    }
}

@include phone-portrait {
	.eltdf-page-footer {
		.eltdf-footer-bottom-holder .eltdf-footer-bottom-inner{
			&.eltdf-full-width,
			&.eltdf-grid{
				padding: 35px 20px;
				font-size: 14px;
			}
			ul li a {
				font-size: 8px;
			}
		}
	}
}
/* ==========================================================================
   Footer responsive style - end
   ========================================================================== */